"use client";
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip } from "recharts";

interface Props {
    categoryData: { category: string; tonnage: number }[];
}

export default function SalesByCategoryPie({ categoryData }: Props) {
    const COLORS = ['#3b82f6', '#10b981', '#f97316', '#8b5cf6', '#ec4899'];

    return (
        <>
            <ResponsiveContainer width="100%" height={200}>
                <PieChart>
                    <Pie
                        data={categoryData}
                        dataKey="tonnage"
                        nameKey="category"
                        innerRadius={50}
                        outerRadius={80}
                        paddingAngle={4}
                    >
                        {categoryData.map((entry, index) => (
                            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                        ))}
                    </Pie>
                    <Tooltip
                        contentStyle={{ backgroundColor: "#fff", border: "none" }}
                        formatter={(value: number) => [`${value.toFixed(2)} tons`, ""]}
                    />
                </PieChart>
            </ResponsiveContainer>
            {/* LEGEND WITH PERCENTAGES */}
            <div className="flex justify-center gap-6 mt-4 text-sm">
                {categoryData.map((entry, index) => (
                    <div key={entry.category} className="flex items-center gap-2">
                        <span
                            className="w-3 h-3 rounded-full"
                            style={{ backgroundColor: COLORS[index % COLORS.length] }}
                        />
                        <span>{`${entry.category} (${((entry.tonnage / categoryData.reduce((sum, item) => sum + item.tonnage, 0)) * 100).toFixed(1)}%)`}</span>
                    </div>
                ))}
            </div>
        </>
    );
}